@import "../../global.scss";

.works{
    background-color:whitesmoke;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 90%;
    

    h1{
        font-size: 50px;
        display: flex;
        position: relative;
        overflow: hidden;
    }

    ul{
        margin: 10px;
        list-style: none;
        display: flex;
    }

    .container{
        width: 70%; 
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        .item{
            width: 220px;
            height: 150px;
            border-radius: 20px;
            border: 1px solid lightgray;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: whitesmoke;
            position: relative;
            transition: all .5s ease;

            h3{
                position: absolute;
                font-size: 20px;
            }

            img{
                width: 60%;
                height: 80%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover{
                background-color: $mainColor;
                img{
                    opacity: 0.2;
                    z-index: 0; 
                }
            }
        }
    }


}