.App {
  height: 100vh;
  .sections{
    width: 100%;
    height: calc(100vh - 70px);
    background-color: whitesmoke;
    position: relative;
    top: 70px;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory; // has to be snap type when scrolling
    scrollbar-width: none; // for firefox
    &::-webkit-scrollbar{
      display: none;
    }

    > *{
      width: 100vw;
      height: calc(100vh - 70px);
      scroll-snap-align: start;
    }
  }

}

